<template>
<div>
    <nav v-if="routeName !== 'Overview' && routeName !== 'Keychain-map'" class="navbar navbar-expand-lg navbar-light">
        <div class="container-fluid fixed-header">
            <ul class="header-content">
                <li>
                    <a class="navbar-brand" href="#">{{routeName}}</a>
                </li>
                <li class="subheader-top">
                    <span>{{getDescription}}</span>
                <li class="filter-container">
                    <br>
                    <button class="transparent" @click="filterShow()">
        <drop-down   
                     title="Filter"
                     title-classes="nav-link specific-dropdown filter-table"
                     customTitleFilter
                     icon="">

          </drop-down>
        </button>
                </li>
                <li>
                    <div v-if="routeName === 'Keychain'" class="selector-item">
                        <router-link to='Keychain'>
                            <p class="item-text"><img class="selector-img" src="@/assets/havr-icon/list.svg"> list </p>
                        </router-link>
                        <router-link to='keychain-map'>
                            <p class="item-text"><img class="selector-img" src="@/assets/havr-icon/map-marker.svg"> Map</p>
                        </router-link>
                    </div>
                </li>
                <transition name="fade">
                    <li class="sub-filter" v-if="showFiltersKeychain && routeName === 'Keychain'">
                        <keychain-filter-table />
                    </li>

                </transition>
            </ul>
            <button class="navbar-toggler navbar-burger filter-button"
              type="button"
              @click="toggleSidebar"
              :aria-expanded="$sidebar.showSidebar"
              aria-label="Toggle navigation">
        <span class="navbar-toggler-bar"></span>
        <span class="navbar-toggler-bar"></span>
        <span class="navbar-toggler-bar"></span>
      </button>
            <div class="collapse navbar-collapse">
                <ul class="navbar-nav ml-auto">
                </ul>
            </div>
        </div>
    </nav>
    <nav v-if="routeName === 'Keychain-map'" class="navbar navbar-expand-lg navbar-light">
        <div class="container-fluid fixed-header">
            <ul class="header-content map-header">
                <li>
                    <input v-if="store.state.map.floor_map.picture" :class="{'overlay-map': store.state.map.floor_map.tutorial }" v-model="store.state.floor_map_name" placeholder="My first map" class="navbar-brand none-border" href="#">
                    <a v-else class="navbar-brand" href="#">Keychain</a>
        </li>
                      
                <li class="filter-container">
                    
                    <button class="transparent" @click="filterShow()">
        <drop-down   
                     title="Filter"
                     title-classes="nav-link specific-dropdown filter-table"
                     customTitleFilter
                     icon="">

          </drop-down>
        </button>
                </li> 
          
                <transition name="fade">
                    <li class="sub-filter" v-if="showFiltersKeychain && routeName === 'Keychain'">
                        <keychain-filter-table />
                    </li>

                </transition>
            </ul>
            <button class="navbar-toggler navbar-burger filter-button"
              type="button"
              @click="toggleSidebar"
              :aria-expanded="$sidebar.showSidebar"
              aria-label="Toggle navigation">
        <span class="navbar-toggler-bar"></span>
        <span class="navbar-toggler-bar"></span>
        <span class="navbar-toggler-bar"></span>
      </button>
            <div class="collapse navbar-collapse">
                <ul class="navbar-nav ml-auto">
                </ul>
            </div>
        </div>
    </nav>
</div>
</template>

<script>
import KeychainFilterTable from "./KeychainFilterTable";
import store from '@/flulux/store'
export default {
    components: {
        KeychainFilterTable
    },
    mounted() {
        this.$root.$on('show_top_name_navbar', (p) => this.showTopMapNavbar = p)
    },
    computed: {
        routeName() {
            const {
                name
            } = this.$route;
            return this.capitalizeFirstLetter(name);
        },
        getDescription() {
            let description = "";
            this.$route.matched.forEach(elm => {
                if (elm.name === this.$route.name) {
                    description = elm.props.default.description;
                }
            });
            return description;
        }
    },
    data() {
        return {
            showFiltersKeychain: false,
            activeNotifications: false,
            showTopMapNavbar: false,
            store: store,
            descriptionNameMap: null
        };
    },

    methods: {
        capitalizeFirstLetter(string) {
            return string.charAt(0).toUpperCase() + string.slice(1);
        },
        filterShow() {
            this.showFiltersKeychain = !this.showFiltersKeychain;
        },
        toggleNotificationDropDown() {
            this.activeNotifications = !this.activeNotifications;
        },
        closeDropDown() {
            this.activeNotifications = false;
        },
        toggleSidebar() {
            this.$sidebar.displaySidebar(!this.$sidebar.showSidebar);
        },
        hideSidebar() {
            this.$sidebar.displaySidebar(false);
        }
    }
};
</script>

<style>
ul {
    list-style-type: none;
}

.header-content {
    margin-top: 0;
    height: 200px;
    position: relative;
    margin-bottom: -4rem;
    left: 20px;
    width: 100%;
    background: white;
}

li {
    list-style-type: none;
}

.subheader-top {
    position: relative;
    bottom: 15px;
}

.none-border {
    border: 0px solid transparent
}

.map-header {
    height: 100px;
}

.filter-container {
    margin-left: -7px;
}

.specific-dropdown {
    width: 95px;
    border-radius: 6px;
    line-height: 15px;
    margin-bottom: 1em;
    height: 32px;
    border: solid 1px #e6e6e6;
}

.left-margin {
    margin-right: 2em;
    width: 200px;
}

.transparent {
    background: transparent;
    border: none;
    position: initial;
}

.fixed-header {
    position: fixed;
    top: -1em;
    background: white;
    width: 70%;
    z-index: 10;
}

.filter-button {
    position: relative;
    right: 8px;
}

.selector-item {
    float: right;
    position: relative;
    bottom: 3em;
    display: flex;
    left: -22em;
}

.item-text {
    font-weight: bold;
    color: #a6a6a6;
    margin-left: 40px;
}

.selector-img {
    margin-right: 16px;
}

.nav-link {
    position: relative;
    top: -7px !important;
}

.filter-section {
    display: flex;
    position: relative;
    top: 1em;
    width: 100%;
    height: 140px;
}

.fade-enter-active,
.fade-leave-active {
    transition: opacity 0.5s;
}

.fade-enter,
.fade-leave-to

/* .fade-leave-active below version 2.1.8 */
    {
    opacity: 0;
}

.container-fluid {
    width: 100%;
    padding-right: 0px !important;
    padding-left: 0px !important
}

.sub-filter {
    border-top: 1px solid #e6e6e6;
    background: white;
    margin-left: -54px;
}
</style>
